#top-navigation .main-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: white;
  display: flex;
  justify-content: start;
}

#top-navigation .main-menu li {
  position: relative;
}

#top-navigation .main-menu > li:has(ul) > a:after {
  content: "\f0d7";
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  margin-left: 8px;
  font-size: 0.8em;
  color: var(--brand-yellow);
}

.homepage .logo-wrapper a,
.homepage .logo-wrapper i {
  color: #fff;
  text-shadow: 0 0 3px #000;
}

#top-navigation .main-menu a {
  display: block;
  padding: 8px 20px 5px 20px;
  text-decoration: none;
  transition: background-color 0.3s;
  font-size: 1rem;
  white-space: nowrap;
}

#top-navigation .main-menu li > a {
  border-bottom: 3px solid transparent;
}

#top-navigation .main-menu li:hover > a {
  border-bottom: 3px solid var(--brand-yellow);
}

#top-navigation .submenu {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 200px;
  display: none;
}

#top-navigation .submenu li {
  width: 100%;
  background-color: var(--brand-gray);
}

#top-navigation .submenu a {
  padding: 8px 20px 5px 16px;
  color: black;
  background-color: var(--brand-gray);
}

#top-navigation .main-menu li:hover > .submenu {
  display: block;
}

/* For the 3rd level menu */
#top-navigation .submenu .submenu {
  top: 0;
  left: 100%;
}

#top-navigation .submenu .submenu a {
  border-left: 1px solid white;
}

#top-navigation .submenu li:hover > a {
  background-color: white;
}

/* mobile navigation */
/* ================================================================= */
.mm-panels {
  --colour-links: var(--system-black);
}
.mm-panels .mm-panel {
  background-color: white;
}
.mm-panels a {
  font-size: 1rem;
}
.mm-panels .mm-btn:after,
.mm-panels .mm-btn:before {
  border-color: var(--brand-yellow) !important;
}
.mm-panels .active a {
  font-weight: bold;
}
.mm-panels > div:first-child .mm-navbar_sticky {
  display: none;
}
.mm-panels .mm-navbar_sticky > a:last-child {
  opacity: 0;
}

.mobile-buttons {
  display: none;
}

.desktop-buttons {
  display: block;
}

@media (max-width: 1200px) {
  .mobile-buttons {
    display: block;
    transform: scale(1.2);
    margin-left: 10px;
    margin-top: 5px;
  }

  .desktop-buttons {
    display: none;
  }
}
.mobile-only {
  display: none !important;
}

.desktop-only {
  display: block;
}

@media (max-width: 768px) {
  .mobile-only {
    display: block !important;
  }

  .desktop-only {
    display: none !important;
  }
}
.preload {
  display: none;
}

.small-print * {
  font-size: 0.95rem;
}

.pre-script {
  display: none !important;
}

.imagery {
  width: 100%;
  display: block;
}

.section {
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 0 auto;
  max-width: var(--max-width);
  margin-bottom: 48px;
}
@media (max-width: 768px) {
  .section {
    margin-bottom: 30px;
  }
}
.section.has-overlap {
  margin-bottom: 60px;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0;
  margin-bottom: 2rem;
  overflow: hidden;
}
.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.column-two {
  padding-bottom: 2rem;
}
@media (min-width: 992px) {
  .column-two {
    columns: 2;
    column-gap: 4rem;
  }
}

.column-three {
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .column-three {
    columns: 2;
    column-gap: 3rem;
  }
}
@media (min-width: 992px) {
  .column-three {
    columns: 3;
    column-gap: 3rem;
  }
}

.external-link {
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .external-link {
    padding-bottom: 2rem;
  }
}

.bttn {
  padding: 12px 16px 10px;
  font-size: 1rem;
  border-radius: 4px;
  cursor: pointer;
  line-height: 1;
  text-decoration: none;
  display: inline-block;
  text-align: center;
  border: 1px solid transparent;
  border: 1px solid var(--brand-red-dark);
  transition: all 0.3s ease-in-out;
  display: inline-flex;
  gap: 12px;
  align-items: center;
  background-color: var(--brand-red-dark);
  color: white;
}
.bttn i {
  color: var(--brand-yellow);
}
@media (hover: hover) {
  .bttn:hover {
    background-color: var(--brand-red);
  }
}
.bttn.small {
  font-size: 0.8rem;
}
.bttn.bordered-bttn {
  border: 1px solid white;
}

/* ================================================================= */
/* .back-to-top */
.back-to-top {
  display: inline-block;
  width: 44px;
  height: 44px;
  text-align: center;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
}
@media (max-width: 992px) {
  .back-to-top {
    display: none;
  }
}
.back-to-top:after {
  content: "\f077";
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  font-size: 1.6em;
  line-height: 44px;
}
@media (hover: hover) {
  .back-to-top:hover {
    cursor: pointer;
    text-decoration: none;
    color: black;
    opacity: 1;
  }
}
.back-to-top.show {
  opacity: 1;
  visibility: visible;
}
@media (min-width: 992px) {
  .back-to-top {
    width: 50px;
    height: 50px;
  }
  .back-to-top:after {
    font-size: 2em;
    line-height: 50px;
  }
}

/* ================================================================= */
/* ================================================================= */
.spacer {
  height: var(--height);
}

#top-navigation {
  margin-top: -160px;
}
#top-navigation.opened {
  margin-top: 0;
}
#top-navigation.scrolled {
  margin-top: -105px;
}

.pod {
  display: flex;
  flex-direction: column;
  background-image: url("../images/slide-bg.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  aspect-ratio: 1.35/1;
  width: 350px;
  margin: 0 auto;
  position: relative;
}
.pod::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.pod .title {
  margin: 0;
  text-align: center;
  z-index: 10;
}
.pod .image-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}
.pod .image-wrapper img {
  max-width: 70%;
  height: auto;
  transition: transform 0.33s ease-in-out;
}
@media (hover: hover) {
  .pod:hover img {
    transform: scale(1.03);
  }
  .pod:hover::after {
    opacity: 0.5;
  }
}

.carousel-main .slide {
  height: 380px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  position: relative;
}
@media (min-width: 640px) {
  .carousel-main .slide {
    height: 660px;
  }
}
.carousel-main .slide:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 30%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0));
}
.carousel-main .slick-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  margin-top: 1rem;
}
.carousel-main .slick-dots button {
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
  background-color: var(--brand-gray);
}
.carousel-main .slick-dots .slick-active button {
  width: 1rem;
  height: 1rem;
  background-color: var(--brand-yellow);
  border-color: var(--brand-yellow);
}
.carousel-main .copy-wrapper {
  position: absolute;
  inset: 0;
  display: flex;
  flex-direction: column;
  justify-content: end;
}
.carousel-main .copy-wrapper .copy-content {
  width: 100%;
  z-index: 10;
  color: white;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0));
}
.carousel-main .copy-wrapper .hero-title {
  margin: 0 auto;
  max-width: var(--max-width);
  font-size: 2rem;
  line-height: 1.2;
  color: white;
  text-shadow: 0 0 3px #000;
  padding: 1rem;
  line-height: 1.2;
}
@media (min-width: 640px) {
  .carousel-main .copy-wrapper .hero-title {
    font-size: 3.2rem;
  }
}
@media screen and (min-width: 768px) {
  .carousel-main .slick-dots {
    max-width: var(--max-width);
    margin: 20px auto 0 auto;
    justify-content: end;
    padding: 0 16px;
  }
  .carousel-main .slick-dots button {
    width: 12px;
    height: 12px;
    border-radius: 2px;
    cursor: pointer;
  }
  .carousel-main .slick-dots .slick-active button {
    width: 60px;
    height: 12px;
    cursor: default;
  }
  .carousel-main .copy-wrapper .copy-content {
    padding-top: 4rem;
    padding-bottom: 1.5rem;
  }
  .carousel-main .copy-wrapper .hero-title {
    padding: 0 12% 0 1rem;
  }
}

.carousel-controls {
  display: flex;
  justify-content: end;
  gap: 8px;
  margin-top: 10px;
  font-size: 2rem;
  color: var(--brand-gray);
}
.carousel-controls button {
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  border: 1px solid var(--brand-gray);
  background-color: white;
  width: 40px;
  height: 40px;
  position: relative;
  border-radius: 2px;
}
.carousel-controls button i {
  position: absolute;
  left: 50%;
  top: 50%;
  font-size: 1.5rem;
  transform: translate(-50%, -50%);
  color: var(--brand-yellow);
}
@media (hover: hover) {
  .carousel-controls button:hover {
    border-color: var(--brand-yellow);
  }
}

.tabs-section .tab-content {
  color: white;
  background-color: var(--brand-red);
  margin-bottom: 2rem;
}
@media screen and (min-width: 768px) {
  .tabs-section .tab-content {
    margin-bottom: 60px;
  }
}
.tabs-section .section-title {
  max-width: var(--max-width);
  margin: 1rem auto;
  padding: 0 16px;
}
.tabs-section .tabs-list {
  display: flex;
  justify-content: center;
  gap: 32px;
  margin-bottom: 16px;
  max-width: var(--max-width);
  margin: 0 auto;
  padding-bottom: 5px;
  padding-left: 4rem;
  padding-right: 4rem;
}
.tabs-section .tabs-list button {
  width: 25%;
  padding: 16px 0;
  line-height: 1;
  font-size: var(--font-subtitle);
  font-weight: 600;
  border-bottom: 8px solid transparent;
  cursor: pointer;
  transition: all 0.33s ease-in-out;
}
.tabs-section .tabs-list button.active {
  border-color: var(--brand-yellow);
  cursor: default;
}
.tabs-section .accordion-header {
  background-color: white;
  color: #000;
  padding: 1rem 1rem 0.3rem 1rem;
}
.tabs-section .accordion-header button {
  font-size: var(--font-subtitle);
  font-weight: 600;
  line-height: 1;
  width: 100%;
  text-align: left;
  border-bottom: 1px solid var(--brand-gray);
  padding-bottom: 10px;
}
.tabs-section .accordion-header.active button {
  border-bottom: 6px solid var(--brand-yellow);
  cursor: default;
}
.tabs-section .tab-hero-content {
  max-width: var(--max-width);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}
.tabs-section .tab-hero-content .title {
  color: white;
  line-height: 1.3;
}
.tabs-section .tab-hero-content .copy-wrapper {
  padding: 1rem;
}
.tabs-section .tab-hero-content .image-wrapper {
  height: 16rem;
  background-size: 120%;
  background-position: top;
  background-repeat: no-repeat;
}
@media screen and (min-width: 768px) {
  .tabs-section .tab-hero-content {
    flex-direction: row;
    gap: 4rem;
  }
  .tabs-section .tab-hero-content .copy-wrapper {
    padding: 5rem 1rem 8rem 1rem;
    max-width: 30%;
  }
  .tabs-section .tab-hero-content .image-wrapper {
    flex-grow: 1;
    height: auto;
    background-size: cover;
    background-position: top center;
  }
}

.with-subsection {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.with-subsection .subsection-content {
  position: relative;
  min-height: 260px;
}
.with-subsection .subsection-content .text-wrapper {
  z-index: 11;
  position: relative;
}
.with-subsection .subsection-content .subsection-bg {
  position: absolute;
  bottom: -20px;
  right: 0;
  z-index: 10;
  max-width: 75%;
}
@media screen and (min-width: 768px) {
  .with-subsection {
    flex-direction: row;
    gap: 2rem;
  }
  .with-subsection .subsection-content {
    min-height: 300px;
  }
  .with-subsection .subsection-content .text-wrapper {
    width: 55%;
  }
}

.footer {
  background-image: url("../images/footer-bg.png");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 200%;
  padding-bottom: 150px;
}
.footer .section {
  margin-bottom: 0;
}
@media screen and (min-width: 768px) {
  .footer {
    background-position: right bottom;
    background-size: contain;
    padding-bottom: 240px;
  }
}
@media (hover: hover) {
  .footer a:hover {
    text-decoration: underline;
  }
}